
import Badge from 'vux/src/components/badge/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import home from '@/img/home.svg'
import wish from '@/img/wish.svg'
import person from '@/img/person.svg'

export default {
components: {
Badge,
ButtonTab,
ButtonTabItem,
Tabbar,
TabbarItem
},
data () {
return {
tab: [
{
name: 'wish',
icon: wish,
badge: 0
},
{
name: 'home',
icon: home,
badge: 0
},
{
name: 'person',
icon: person,
badge: 0
}
]
}
},
created () {
window.$event.on('layout-tabbar:badge', ({ name, badge }) => {
if (name === 'home') {
this.tab[0].badge = badge
}
if (name === 'person') {
this.tab[1].badge = badge
}
})
},
methods: {
click ({ name }) {
this.$replace(name)
}
}
}



