import avatar from './img/avatar.jpg'

export default {
  props: {
    width: {
      type: Number,
      default: 0
    },
    height: {
      type: Number,
      default: 0
    }
  },
  computed: {
    style () {
      return {
        width: `${this.width}px`,
        height: `${this.height}px`
      }
    },
    avatar () {
      return this.$storage.user.avatar || avatar
    }
  }
}



