export default {
  mounted () {
    this.$refs.view.scrollTop = 1
  },
  methods: {
    touchstart () {
      const { view } = this.$refs
      const { scrollTop, scrollHeight, offsetHeight } = view
      if (scrollTop === 0) {
        view.scrollTop = 1
        return
      }
      if (scrollTop === scrollHeight - offsetHeight) {
        view.scrollTop = scrollHeight - offsetHeight - 1
      }
    }
  }
}



