
import XHeader from 'vux/src/components/x-header/index.vue'

import navigationEffectTitle from '@/navigation/effect-title'
import navigationEffectHeader from '@/navigation/effect-header'

export default {
props: {
title: {
type: String,
default: ''
},
right: {
type: String,
default: ''
}
},
components: {
XHeader,
navigationEffectTitle,
navigationEffectHeader
},
computed: {
showBack () {
return !this.$slots.left
},
backText () {
return this.$t('layout-navbar@back')
},
preventGoBack () {
return true
},
showMore () {
return this.right === 'more'
}
},
methods: {
click (event) {
if (event.target.matches('.vux-header-left *')) {
let back = true
this.$emit('tap', {
type: 'left',
preventDefault () {
back = false
}
})
if (back) {
this.$pop()
}
}
if (event.target.matches('.vux-header-right *')) {
this.$emit('tap', {
type: 'right'
})
}
if (event.target.matches('.vux-header-title *')) {
this.$emit('tap', {
type: 'title'
})
}
}
}
}



